import React,{useState} from 'react';

const Lingo = (props)=>{
  return(  
    <>
    <div id="language" class="dropdown">
      <b>
      <a href="#language">{props.menu[45+props.lang]}</a>  
      <br/>
      <button class="dropbtn">
        {(props.lang==0)&&<a>{props.menu[111+props.lang]}</a>}
        {(props.lang==1)&&<a>{props.menu[114+props.lang]}</a>}
        {(props.lang==2)&&<a>{props.menu[117+props.lang]}</a>}
      </button>
      <div class="dropdown-content">
        {(props.lang!=0)&&<a onClick={()=>{props.handleLang(0)}}>{props.menu[111+props.lang]}</a>}
        {(props.lang!=1)&&<a onClick={()=>{props.handleLang(1)}}>{props.menu[114+props.lang]}</a>}
        {(props.lang!=2)&&<a onClick={()=>{props.handleLang(2)}}>{props.menu[117+props.lang]}</a>}
      </div>
      </b>
    </div>
    </>
  )
}

export default Lingo;