import React,{useState} from 'react';

const Services = (props)=>{
  const [services,setservices] = useState('Support');
  function selectservices(x){
    setservices(x);
    props.handlePage(x);
  }
  return(
    <>
    <div id="services" class="dropdown">
      <b>
      <a href="#services">{props.menu[144+props.lang]}</a>  
      <br/>
      <button class="dropbtn"><a href="#selectservices">{props.menu[144+props.lang]}</a></button>
      <div class="dropdown-content">
        <a href={props.menu[147+props.lang]} onClick={()=>{selectservices('it')}}>{props.menu[147+props.lang]}</a>
        <a href={props.menu[150+props.lang]} onClick={()=>{selectservices('finance')}}>{props.menu[150+props.lang]}</a>
        <a href={props.menu[153+props.lang]} onClick={()=>{selectservices('nlp')}}>{props.menu[153+props.lang]}</a>
      </div>
      </b>
    </div>
    </>
  )
}

export default Services;