import React,{useState} from 'react';

const CV = (props)=>{
  const [cv,setCV] = useState('cv');
  function selectCV(x){
    setCV(x);
    props.handlePage(x);
  }
  return(
    <>
    <div id="CV" class="dropdown">
      <b>
      <a href="#CV">{props.menu[51+props.lang]}</a>  
      <br/>
      <button class="dropbtn"><a href="#selectCV">{props.menu[51+props.lang]}</a></button>
      <div class="dropdown-content">
          <a href="#education" onClick={()=>{selectCV('education')}}>{props.menu[6+props.lang]}</a>
          <a href="#certificates" onClick={()=>{selectCV('certificates')}}>{props.menu[9+props.lang]}</a> 
          <a href="#experience" onClick={()=>{selectCV('experience')}}>{props.menu[12+props.lang]}</a>
          <a href="#projects" onClick={()=>{selectCV('projects')}}>{props.menu[15+props.lang]}</a>
          <a href="#skills" onClick={()=>{selectCV('skills')}}>{props.menu[18+props.lang]}</a>
      </div>
      </b>
    </div>
    </>
  )
}

export default CV;