import React,{useState} from 'react';

const Thanks = (props)=>{
  const [thanks,setThanks] = useState('Support');
  function selectThanks(x){
    setThanks(x);
    props.handlePage(x);
  }
  return(
    <>
    <div id="thanks" class="dropdown">
      <b>
      <a href="#thanks">{props.menu[126+props.lang]}</a>  
      <br/>
      <button class="dropbtn"><a href="#selectThanks">{props.menu[120+props.lang]}</a></button>
      <div class="dropdown-content">
        <a href={props.menu[141+props.lang]} onClick={()=>{/*selectThanks('parents')*/}}>{props.menu[138+props.lang]}</a>
        <a href={props.menu[60+props.lang]} onClick={()=>{selectThanks('friends')}}>{props.menu[60+props.lang]}</a>
        <a href={props.menu[141+props.lang]} onClick={()=>{selectThanks('colleagues')}}>{props.menu[141+props.lang]}</a>
         {/*<a href={props.menu[131+props.lang]} onClick={()=>{selectThanks('sbu')}}>{props.menu[129+props.lang]}</a>
         <a href={props.menu[134+props.lang]} onClick={()=>{selectThanks('ask')}}>{props.menu[132+props.lang]}</a>*/}
         <a href={props.menu[135+props.lang]} onClick={()=>{selectThanks('edu')}}>{props.menu[135+props.lang]}</a>
      </div>
      </b>
    </div>
    </>
  )
}

export default Thanks;