import React,{useState,useEffect} from 'react';
import About from './About';
import CV from './CV';
import Services from './Services';
import Support from './Support';
import Fan from './Fan';
import Thanks from './Thanks';
import Links from './Links';
import Lingo from './Lingo';

const Header = (props)=>{
  const [name,setName] = useState('');
  const [head,setHead] = useState([]);
  const [view,setView] = useState(true);
  const [sign,setSign] = useState('');
  function viewHead(){
    (view)?setView(false):setView(true);
  }
 
  useEffect(()=>{
    const url ='https://spreadsheets.google.com/feeds/list/1EYJ9WPjqKqe26InIhdzyFKZzBKgAKRkM04w7aaFQhxk/od6/public/values?alt=json';
    fetch(url) 
    .then(res => res.json())
    .then(data=>data.feed.entry)
    .then(entry=>{
      let items = [];
      entry.forEach(e=>{
        let index = 'gsx$'+'head'
        let item = e[index]['$t'];
        if(item.length>0) items.push(item);
      });
      setName(items[props.lang]);
      setHead(items); 
      document.title=name;
    });
  });
  return(
    <>
    <div className="header f">
    <b>
     <br/>
	   <h1>{name}</h1>
     <br/>
      {view&&<div id="subhead">
      <div>
        <About menu={head} lang={props.lang} handlePage={props.handlePage}/>
        <CV menu={head} lang={props.lang} handlePage={props.handlePage}/>
        <Services menu={head} lang={props.lang} handleLang={props.handleLang}/>
        {/*<Links menu={head} lang={props.lang} handleLang={props.handleLang}/>*/}
        <br/>
              <Support menu={head} lang={props.lang} handleLang={props.handleLang}/>
        {/*<Fan menu={head} lang={props.lang} handleLang={props.handleLang}/>*/}
              <Lingo menu={head} lang={props.lang} handleLang={props.handleLang}/>
              <Thanks menu={head} lang={props.lang} handleLang={props.handleLang}/>
      </div>
      </div>
      }
      <div id="x">
        <a href="#x" onClick={()=>{viewHead()}}>{(view)?<span>&#10006;</span>:<span>☰</span>}</a>  
      </div>
      </b>
      </div>
    </>
  )
}

export default Header;