import React,{useState} from 'react';

const Support = (props)=>{
  const [support,setSupport] = useState('Support');
  function selectSupport(x){
    setSupport(x);
    props.handlePage(x);
  }
  return(
    <>
    <div id="support" class="dropdown">
      <b>
      <a href="#support">{props.menu[57+props.lang]}</a>  
      <br/>
      <button class="dropbtn"><a href="#selectSupport">{props.menu[123+props.lang]}</a></button>
      <div class="dropdown-content">
         <a href={props.menu[67+props.lang]} onClick={()=>{selectSupport('nihil')}}>{props.menu[63+props.lang]}</a>
         <a href={props.menu[72+props.lang]} onClick={()=>{selectSupport('dunlap')}}>{props.menu[69+props.lang]}</a>
         <a href={props.menu[78+props.lang]} onClick={()=>{selectSupport('alvarez')}}>{props.menu[75+props.lang]}</a>{/*
         <a>---</a>
         <a href={props.menu[90+props.lang]} onClick={()=>{selectSupport('techlead')}}>{props.menu[87+props.lang]}</a>
         <a href={props.menu[96+props.lang]} onClick={()=>{selectSupport('jomatech')}}>{props.menu[93+props.lang]}</a>
         <a href={props.menu[102+props.lang]} onClick={()=>{selectSupport('hellomayuko')}}>{props.menu[99+props.lang]}</a>
         <a href={props.menu[108+props.lang]} onClick={()=>{selectSupport('zhangsta')}}>{props.menu[105+props.lang]}</a>*/}
      </div>
      </b>
    </div>
    </>
  )
}

export default Support;